.card {
    background: #fdfdfd;
    border: 1px solid #eee;
    border-radius: 10px;
    padding: 16px;
    box-shadow: 0 2px 8px rgba(0,0,0,0.05);
    transition: transform 0.2s ease;
  }
  
  .card:hover {
    transform: scale(1.02);
  }
  
  .card h2 {
    font-size: 1.2rem;
    margin-bottom: 10px;
  }
  
  .card a {
    display: inline-block;
    margin-top: 10px;
    color: #007bff;
  }
  